export function checkingUserName() {
  const user = localStorage.getItem('user');
  return user !== null && Object.keys(JSON.parse(user)).length !== 0;
}

export function checkingPathNameURL(path) {
  return [
    { name: '', path: 'popular' },
    { name: '', path: 'pizza' },
    { name: '', path: 'sushi' },
    { name: '', path: 'burrito' },
    { name: '', path: 'taco' },
    { name: '', path: 'desserts' },
    { name: '', path: 'favorite' },
  ].filter((el) => path.includes(el.path) && el)[0];
}
